<template>
	<div class="integral">
		<van-nav-bar fixed border title="积分中心" left-arrow @click-left="goBack" />
		<div class="integral_content">
			<div class="integral_num">
				<div class="integral_mr">
					<p><span class="left">积分余额:</span><span class="right" @click="showTips">查看积分说明<i class="iconfont iconyou"></i></span></p>
					<p class="integral_size">{{userInfo.integral}}</p>
					<div class="integral_tx">
						<p>积分余额1000分以上才能提现</p> <!-- 详见规则说明。 -->
						<p class="tx_btn" @click="goCash">提现</p>
					</div>
				</div>
			</div>
		</div>
		<van-cell-group class="celltop">
			<van-cell class="integral_title" title="积分明细" />
			<van-cell v-for="(item,index) in list" :key='index'>
				<div slot="title">
					<van-col span="12">{{ getTypeName(item.type,item) }}</van-col>
					<van-col span="12" style="display: flex; justify-content: flex-end;">{{item.n_integral}}</van-col>
				</div>
				<div slot="label">
					<van-col span="12">{{moment.unix(item.add_time).format("YY-MM-DD HH-mm")}}</van-col>
					<van-col span="12" style="display: flex; justify-content: flex-end;">积分余额:{{item.p_integral}}</van-col>
				</div>
			</van-cell>
			<van-button type="warning" size="large" block to="/integral/recharge">积分充值/开通VIP</van-button>
		</van-cell-group>
		<van-dialog v-model="showKF" title="温馨提示" messageAlign="center" >
		  <img class="dialog-img" src="../../../public/img/img5.jpg">
		  <p style="text-align: center;">长按或扫一扫添加微信</p>
		  <p style="text-align: center;"> 提现需求已提交,请等待工作人员与您联系。如有<br/>问题可联系平台管理员<br/>(电话:15602908103)</p>
		</van-dialog>
	</div>
</template>

<script>
	import moment from "moment";
	import {
		mapGetters
	} from "vuex";
	import {
		Col,
		Row,
		NavBar,
		Cell,
		CellGroup,
		Button
	} from "vant";
	import {
		getScoreRecords
	} from "@/api/base";
	import {
		cashOutCreate
	} from "@/api/v4.js";

	export default {
		components: {
			[NavBar.name]: NavBar,
			[Col.name]: Col,
			[Row.name]: Row,
			[Cell.name]: Cell,
			[CellGroup.name]: CellGroup,
			[Button.name]: Button,

		},
		data() {
			return {
				moment,
				list: [],
				page: 0,
				limit: 20,
				showKF:false,
			}
		},
		computed: {
			...mapGetters(["userInfo"])
		},
		async mounted() {
			this.list = await getScoreRecords({
				user_id: this.userInfo.id,
				page: this.page,
				limit: this.limit
			});
		},
		methods: {
			goBack() {
				this.$router.back();
			},
			showTips() {
				this.$dialog.alert({
					title: "积分使用说明（20200402）",
					messageAlign:"left",
					message: "<strong>1.积分消耗:</strong>对接需求,可以使用积分支付,消耗10积分;\n<strong>2.名片交换</strong>,可以使用积分支付,消耗5积分\n<strong>3.积分增加:</strong>\na.新注册天集社用户,赠送50积分; \nb.个人中心名片认证申请通过, 赠送50积分; \nc.公司认证审核通过, 赠送100积分; \nd.成功发布一个需求, 赠送20积分; \ne.通过邀请海报分享, 每成功邀请一名新用户注册可获赠50积分;\nf.邀请注册的好友充值限时获得10 % 比例积分奖励;\ng.充值可获得相应积分。",
				})
			},
			goCash() {
				if (this.userInfo.integral == 1000) {
					cashOutCreate().then(res => {
						if (res == "") {
							this.showKF = true
						} else {
							this.$dialog.alert({
								title: "温馨提示",
								message: "您没有此权限,请联系平台管理员\n(电话:15602908103)",
							})
						}
					})
				} else {
					this.$dialog.alert({
						title: "温馨提示",
						message: "不符合提现标准",
					})
				}


			},
			getTypeName(type, item) {
				switch (type) {
					case "0":
						return "积分申请";
					case "1":
						return "定向申请";
					case "2":
						return "需求申请";
					case "3":
						return "用户注册";
					case "4":
						return "交换名片";
					case "5":
						return "个人认证通过积分";
					case "6":
						return "需求通过";
					case "7":
						return item.title;
					case "8":
						return "充值积分";
					case "9":
						return item.title;
					default:
						return "未知操作";
				}
			}
		}
	}
</script>

<style scoped>
	.integral_title {
		font-size: 17px;
		font-weight: bold;
		color: rgba(51, 51, 51, 1);
	}

	.page {
		padding-top: initial;
	}

	.integral {
		background-color: #f6f6f6;
	}

	.integral_content {
		margin-top: 60px;
		background-color: #fff;
		padding: 22px 0;
	}

	.integral_num {
		width: 90%;
