		font-size: 12px;
	}

	.tx_btn {
		width: 73px;
		height: 26px;
		background: rgba(255, 255, 255, 1);
		border: 1px solid rgba(203, 4, 35, 1);
		border-radius: 18px;
		text-align: center;
		line-height: 26px;
		color: #666;
		position: absolute;
		bottom: 10px;
		right: 0;
	}

	.iconyou {
		margin-left: 5px;
	}

	.celltop {
		margin-top: 10px;
		padding-bottom: 20px;
		margin-bottom: 40px;
	}

	.van-button--warning {
		color: #fff;
		background-color: #CB0423;
		border: 1px solid #CB0423;
		position: fixed;
		bottom: 0;
		left: 0;
		z-index: 2;
	}

	.van-button {
		/* border-radius: 10px; */
	}
	.dialog-img{
		display: block;
		width: 150px;
		height:150px;
		margin:0 auto;
	}
	@media (min-width:750px) {
		.dialog-img{
			display: block;
			width: 150px;
			height:150px;
			margin:0 auto;
		}
